import React from "react";
import { NavLink } from "react-router-dom";

export const Gift = () => {
  return (
    <NavLink to="/codelab/gift" className="gift">
      <div className="gift__wrap">
        <div className="gift__image--wrap">
          <img
            src={`${process.env.PUBLIC_URL}/images/icon/gift.svg`}
            alt=""
            className="gift__icon"
          />
        </div>
        <div className="gift__content">
          <h3 className="gift__title">Mn qu nho nh ny dnh cho bn!</h3>
          <p className="gift__desc line-clamp">
            Chng ti s m bo rng tt c cc sn phm c sn trong n hng
            ca bn s c ng gi cn thn v vn chuyn trc tip n a
            ch ca ngi nhn
          </p>
        </div>
      </div>
    </NavLink>
  );
};
