export const Validation = (values) => {
  let errors = {};
  // Kim tra email vi min @parent.com
  const email__require =
    /^(([^<>()[\]\\.,;:\s@"]+(\.[^<>()[\]\\.,;:\s@"]+)*)|.(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;
  const password__require =
    /^(?=.*[A-Z])(?=.*[a-z])(?=.*\d)(?=.*[@$!%*?&#])[A-Za-z\d@$!%*?&#]{8,}$/;

  if (!values.email) {
    errors.email = "Email khng chnh xc";
  } else if (!email__require.test(values.email)) {
    errors.email = "Email bn nhp khng hp l !";
  }
  if (!values.password) {
    errors.email = "Mt khu khng chnh xc ";
  } else if (!password__require.test(values.password)) {
    errors.password = "Mt khu khng hp l !";
  }
  if (values.name === "") {
    errors.name = "Name is not empty !!";
  }
  if (values.email === "") {
    errors.email = "Email is not empty !!";
  }
  if (values.subject === "") {
    errors.subject = "Subject is not empty !!";
  }
  // Cc kim tra khc...
  console.log(errors);
  return errors;
};
